

.boxmodel{
	width: 400px;
	margin: auto;
	margin-top: 100px;
	border: 1px solid lightgrey;
	overflow: hidden;
	box-shadow: 2px 2px 1px #aaaaaa;
}
.boxmodel_topbar{
	display: flex;
	background-color: #f0ecec;
}
.boxmodel_title{
	margin: 0;
	background-color: var(--default-color);
	transform: skewX(-24deg);
	width: 200px;
	padding: 5px 5px 5px 30px;
	position: relative;
	right: 12px;
}

.boxmodel_title > * { 
	transform: skewX(24deg); 
}
.boxmodel_title p{
	margin: 0;
	margin-left: 10px;
	font-family: 'Bebas Neue', cursive;
	font-size: 25px;
	color: rgba(248,248,248,255);
}
.extra{
	width: 5px;
	background-color: var(--default-color);
	transform: skewX(-24deg);
	position: relative;
	right: 12px;
	margin-left: 5px; 
}
.boxmodel_body{
	padding: 30px 50px;
}
.form{
	display: flex;
	flex-direction: column;
}
.form input[type=email],input[type=password] {
	height: 50px;
	border: none;
	border-bottom: 1px solid grey;
	text-align: center;
	padding-top: 20px; 
	color: rgba(91,91,91,255);
	font-family: 'Open Sans', sans-serif;
	font-weight: 700;
}
.form input:focus{
	outline: none;
}
.form input::placeholder{
	text-align: center;
	font-size: 12px;
	font-weight: 400;
}
.form input:first-child{
	margin-bottom: 10px;
}
.form a{
	text-align: right;
	text-decoration: none;
	color: rgba(91,91,91,255);
	font-family: 'Open Sans', sans-serif;
	font-weight: 400;
	font-size: 12px;
	margin: 5px 0px 20px 0px;
}
.form input[type=submit]{
	height: 40px;
	border: none;
	border-radius: 10px;
	text-align: center;
	color: white;
	font-family: 'Open Sans', sans-serif;
	font-weight: 700;
	background-color: var(--default-color);
	cursor: pointer;
}

.afterform {
	margin-top: 30px;
}
.socialtext p{
   width: 100%; 
   text-align: center; 
   border-bottom: 1px solid grey; 
   line-height: 0.1em;
   margin: 10px 0 20px; 
}
.socialtext p span{
	background:#fff; 
    padding:0 10px; 
    color: rgba(91,91,91,255);
	font-family: 'Open Sans', sans-serif;
	font-weight: 400;
	font-size: 10px;
}
.sociallinks{
	display: grid;
	grid-template-columns: 48% 48%;
	justify-content: space-between;
}
.sociallinks img{
	width: 100%;
	height: auto;
}
.signup{
	text-align: center;
	color: rgba(91,91,91,255);
	font-family: 'Open Sans', sans-serif;
	font-weight: 400;
	font-size: 10px;
}
.signup a{
	text-decoration: underline;
	color: var(--default-color);
	font-family: 'Open Sans', sans-serif;
	font-weight: 700;
	font-size: 10px;
}


